            transform: rotate(360deg);
  }
}



/* MENU NAVEGACION */

.toggle-menu {
  position: fixed;
  right: 20px;
  top: 00px;
  font-size: 3em;
}
ul {
  list-style-type: none;
}
.menu {
  width: 250px;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgb(240, 240, 240);
  text-align: center;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  z-index: 9999;
}
.menu.abierto {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.menu img {
  width: 150px;
  display: block;
  margin: 20px auto;
  border-bottom: 1px solid rgba(0, 0, 0, .1);
  padding-bottom: 20px;
}
.menu nav a {
  display: block;
  padding: 10px 0;
}

@media all and (min-width: 670px) {
  .toggle-menu {
  display: none;
  }
  .menu {
  width: 100vw;
  height: 50px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: none;
  transition: none;
  }
  .menu.abierto {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  }
  .menu img {
  margin: 8px;
  width: 100px;
  float: left;
  border: none;
  }
  .menu ul {
  float: right;
  }
  .menu li {
  display: inline-block;
  margin: 5px;
  }
  .menu h1 {
  text-align: left;
  font-size: 1.5em;
  font-weight: 300;
  float: left;
  line-height: 50px;
  margin-left: 10px;
}
