    text-transform: uppercase;
    text-decoration: none;
    background: transparent;
    border:none;
}
ul{
    margin-right: 2vw;
}
@media all and (min-width: 670px) {

    .toggle-menu {
        display: none;
    }
    .menu {
        width: 100vw;
        height: 50px;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        -webkit-transition: none;
        transition: none;
        background-color: #fff;
    }
    .menu.abierto {
        -webkit-transform: translateX(0);
